#create-nft-page .textfield-create-nft {
  display: flex;
  flex-direction: column;
  margin: 25px 0;
}

#create-nft-page .textfield-create-nft label {
  font-weight: 700;
}

#create-nft-page .textfield-create-nft textarea {
  resize: none;
  padding: 8.5px 14px;
  border-radius: 10px;
  background: var(--light-gray);
}

#create-nft-page .textfield-create-nft textarea::placeholder {
  font-size: 12px;
}

#create-nft-page .textfield-create-nft textarea:focus {
  outline: none;
  border: 2px solid var(--second-color-hover);
}

#create-nft-page .nft-collection {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

#create-nft-page .nft-collection button,
#create-nft-page .upload-button {
  border: none;
  background: linear-gradient(
    var(--purple-hover-color) 20%,
    var(--purple-color) 100%
  );
  color: var(--white-color);
  border-radius: 15px;
  padding: 12px 25px;
  cursor: pointer;
}

#create-nft-page .nft-collection button:hover,
#create-nft-page .upload-button:hover {
  background: var(--purple-hover-color);
}

#create-nft-page .create-nft-img-container {
  background: var(--light-gray);
  border: 3px solid var(--black-color);
  border-radius: 14px;
  height: 90%;
  max-width: 500px;
  margin: 10px auto;
  max-height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

#create-nft-page .upload-button input {
  display: none;
}

#create-nft-page {
  background-color: var(--light-white-color);
}

@media (max-width: 767px) {
  #create-nft-page .create-nft-img-container {
height: 400px;
  }
}
